import React from "react";
import { Container, Typography, makeStyles, Divider, useTheme, Box, useMediaQuery } from "@material-ui/core";
import Social from "../Social";
import { GitHub, LinkedIn, YouTube, Instagram, Email } from "@material-ui/icons";
import IconBtn from "../IconBtn";
import { useTranslation } from "react-i18next";

const Footer = () => {
    const classes = useStyles();
    const theme = useTheme();
    const { t } = useTranslation();
    const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

    return (
        <Container component="section" className={classes.footerContainer}>
            
            {/*
            OLD VERSION
            <Box className={classes.footerBox}>
                {isMobile && <Social mobile />}
                <Typography variant="body2" color="initial">
                    © 2024 Filipe Lopes Pires
                </Typography>
            </Box>
            */}

            {/*
            VERSION 1
            <Box display="flex" alignItems="center" className={classes.amdg}>
                <Divider style={{ flexGrow: 1, backgroundColor: theme.palette.primary.main }}/>
                <Typography align="center" variant="h4" style={{ margin: '0 10px' }}>
                    AMDG
                </Typography>
                <Divider style={{ flexGrow: 1, backgroundColor: theme.palette.primary.main }} />
            </Box>
            */}

            {/*
            VERSION 2
            */}
            <Divider style={{ flexGrow: 1, backgroundColor: theme.palette.primary.main }}/>
            <Typography align="center" variant="h4" className={classes.amdg}>
                AMDG
            </Typography>
            <Divider style={{ flexGrow: 1, backgroundColor: theme.palette.primary.main }}/>

            {isMobile ? (
                <>
                <Box className={classes.footerBoxMobileTop} display="flex" justifyContent="space-between" alignItems="center">
                    <Typography variant="body2" color="initial" className={classes.footerText}>
                        <IconBtn icon={Email} m={1} href="mailto:fsnap@protonmail.com" className={`${classes.socialIcon} ${classes.emailIcon}`} />fsnap@protonmail.com
                    </Typography>
                    <Typography variant="body2" color="initial" className={classes.footerText} style={{ display: 'block !important' }}>
                        <IconBtn icon={GitHub} m={1} href="https://github.com/FilipeLopesPires" className={`${classes.socialIcon} ${classes.nonEmailIcon}`}/>
                        <IconBtn icon={LinkedIn} m={1} href="https://www.linkedin.com/in/FilipeLopesPires/" className={`${classes.socialIcon} ${classes.nonEmailIcon}`}/>
                        <IconBtn icon={YouTube} m={1} href="https://www.youtube.com/@filipelopespires" className={`${classes.socialIcon} ${classes.nonEmailIcon}`}/>
                        <IconBtn icon={Instagram} m={1} href="https://www.instagram.com/FilipeLopesPires/" className={`${classes.socialIcon} ${classes.nonEmailIcon}`}/>
                    </Typography>
                </Box>
                <Box className={classes.footerBoxMobileBottom} display="flex" justifyContent="space-between" alignItems="center">
                    <Typography variant="body2" color="initial" className={classes.footerText}>
                        © 2024 Filipe Lopes Pires
                    </Typography>
                    <Typography variant="body2" color="initial" className={classes.footerText}>
                        {t("footer_location_prefix")} <b>{t("footer_location")}</b>
                    </Typography>
                </Box>
                </>
            ) : (
                <>
                <Box className={classes.footerBox} display="flex" justifyContent="space-between" alignItems="center">
                    <Typography variant="body2" color="initial" className={classes.footerText}>
                        © 2024 Filipe Lopes Pires
                    </Typography>
                    <Typography variant="body2" color="initial" className={classes.footerText}>
                        <IconBtn icon={Email} m={1} href="mailto:fsnap@protonmail.com" className={`${classes.socialIcon} ${classes.emailIcon}`} />fsnap@protonmail.com
                    </Typography>
                    <Typography variant="body2" color="initial" className={classes.footerText}>
                        <IconBtn icon={GitHub} m={1} href="https://github.com/FilipeLopesPires" className={classes.socialIcon}/>
                        <IconBtn icon={LinkedIn} m={1} href="https://www.linkedin.com/in/FilipeLopesPires/" className={classes.socialIcon}/>
                        <IconBtn icon={YouTube} m={1} href="https://www.youtube.com/@filipelopespires" className={classes.socialIcon}/>
                        <IconBtn icon={Instagram} m={1} href="https://www.instagram.com/FilipeLopesPires/" className={classes.socialIcon}/>
                    </Typography>
                    <Typography variant="body2" color="initial" className={classes.footerText}>
                        {t("footer_location_prefix")} <b>{t("footer_location")}</b>
                    </Typography>
                </Box>
                </>
            )}
            
        </Container>
    );
};



const useStyles = makeStyles((theme) => ({
    footerContainer: {
        maxWidth: theme.breakpoints.values.xl,
    },
    footerText: {
        [theme.breakpoints.down("sm")]: {
            fontSize: "18px",
        },
        [theme.breakpoints.down("xs")]: {
            fontSize: "14px",
        },
    },
    footerBox: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(4), //(2.5),
    },
    footerBoxMobileTop: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(1), //(2.5),
    },
    footerBoxMobileBottom: {
        paddingTop: theme.spacing(1),
        paddingBottom: theme.spacing(4), //(2.5),
    },
    amdg: {
        marginTop: "15vh",
        marginBottom: "15vh",
        textTransform:"uppercase",
        whiteSpace: "nowrap",
        color: theme.palette.primary.main,
    },
    socialIcon: {
        "& svg": {
            marginBottom: "-6px",
            fontSize: "24px",
            [theme.breakpoints.down("sm")]: {
                fontSize: "24px",
            },
            [theme.breakpoints.down("xs")]: {
                fontSize: "20px",
            },
        }
    },
    emailIcon: {
        marginBottom: "10px",
        "& svg": {
            marginLeft: "-10px"
        }
    },
    nonEmailIcon: {
        marginBottom: "10px",
        "& svg": {
            marginRight: "-10px"
        }
    },
    
}));

export default Footer;
